
.textarea:before{content:"";z-index:1;position:absolute;bottom:0;left:0;width:100%;height:100%;border:1px solid #ccc;box-sizing:border-box;pointer-events:none
}
.textarea.has-focus{outline:2px solid -webkit-focus-ring-color
}
.textarea.has-focus:before{border-color:#66f
}
textarea{display:block;width:100%;min-height:2em;padding:0 0.5em;line-height:2;resize:none;border:0;background:linear-gradient(to bottom, #fff 0, #fff calc(2em - 1px), #ccc calc(2em - 1px), #ccc 2em, #fff 2em);background-repeat:repeat-y;background-size:100% 2em
}
textarea:focus{outline:0
}



