
.scroller{position:relative
}
.scroller__inner{position:relative;overflow-x:hidden;overflow-y:auto
}
.scroller__inner::-webkit-scrollbar{width:12px;height:12px
}
.scroller__inner::-webkit-scrollbar-track{background:#fff
}
.scroller__inner::-webkit-scrollbar-thumb{background:#555;border:4px solid #fff
}



